"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Textarea } from "@/components/ui/textarea";
import { toast } from "sonner";

// Mock data for gates
const mockGates = [
  { id: "1", name: "Gate A - Main Entry", direction: "entry", active: true },
  { id: "2", name: "Gate B - Main Exit", direction: "exit", active: true },
  { id: "3", name: "Gate C - Side Entry", direction: "entry", active: true },
  { id: "4", name: "Gate D - Emergency Exit", direction: "exit", active: false }
];

// Mock data for recent scans
const mockRecentScans = [
  {
    id: "1",
    vehiclePlate: "MH12AB1234",
    gateName: "Gate A - Main Entry",
    direction: "entry",
    timestamp: new Date(Date.now() - 5 * 60 * 1000).toISOString(),
    result: "allow",
    mode: "qr",
    operatorName: "John Guard"
  },
  {
    id: "2",
    vehiclePlate: "MH14CD5678",
    gateName: "Gate B - Main Exit",
    direction: "exit",
    timestamp: new Date(Date.now() - 10 * 60 * 1000).toISOString(),
    result: "allow",
    mode: "manual",
    operatorName: "Jane Guard"
  },
  {
    id: "3",
    vehiclePlate: "MH16EF9012",
    gateName: "Gate A - Main Entry",
    direction: "entry",
    timestamp: new Date(Date.now() - 15 * 60 * 1000).toISOString(),
    result: "deny",
    mode: "qr",
    operatorName: "John Guard",
    notes: "Pass expired"
  }
];

export default function ScanPage() {
  const [recentScans, setRecentScans] = useState(mockRecentScans);
  const [currentTime, setCurrentTime] = useState(new Date());

  // QR Scan form state
  const [qrScanForm, setQrScanForm] = useState({
    qrCode: "",
    gateId: "",
    notes: ""
  });

  // Manual scan form state
  const [manualScanForm, setManualScanForm] = useState({
    vehiclePlate: "",
    gateId: "",
    notes: ""
  });

  // Update current time every second
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  const handleQrScan = () => {
    if (!qrScanForm.qrCode || !qrScanForm.gateId) {
      toast.error("Please fill in QR code and select a gate");
      return;
    }

    const selectedGate = mockGates.find(gate => gate.id === qrScanForm.gateId);
    if (!selectedGate) {
      toast.error("Invalid gate selected");
      return;
    }

    // Simulate QR code validation
    const isValidQr = qrScanForm.qrCode.startsWith("PARK-");
    const vehiclePlate = isValidQr ? qrScanForm.qrCode.replace("PARK-", "") : "UNKNOWN";

    const scanResult = {
      id: (recentScans.length + 1).toString(),
      vehiclePlate: vehiclePlate,
      gateName: selectedGate.name,
      direction: selectedGate.direction,
      timestamp: new Date().toISOString(),
      result: isValidQr ? "allow" : "deny",
      mode: "qr",
      operatorName: "Current User",
      notes: qrScanForm.notes || (isValidQr ? "" : "Invalid QR code")
    };

    setRecentScans([scanResult, ...recentScans]);
    setQrScanForm({ qrCode: "", gateId: "", notes: "" });
    
    if (isValidQr) {
      toast.success(`‚úÖ Access granted for ${vehiclePlate} at ${selectedGate.name}`);
    } else {
      toast.error(`‚ùå Access denied - Invalid QR code`);
    }
  };

  const handleManualScan = () => {
    if (!manualScanForm.vehiclePlate || !manualScanForm.gateId) {
      toast.error("Please fill in vehicle plate and select a gate");
      return;
    }

    const selectedGate = mockGates.find(gate => gate.id === manualScanForm.gateId);
    if (!selectedGate) {
      toast.error("Invalid gate selected");
      return;
    }

    // Simulate pass validation (simple check for demo)
    const hasValidPass = Math.random() > 0.3; // 70% chance of valid pass

    const scanResult = {
      id: (recentScans.length + 1).toString(),
      vehiclePlate: manualScanForm.vehiclePlate.toUpperCase(),
      gateName: selectedGate.name,
      direction: selectedGate.direction,
      timestamp: new Date().toISOString(),
      result: hasValidPass ? "allow" : "deny",
      mode: "manual",
      operatorName: "Current User",
      notes: manualScanForm.notes || (hasValidPass ? "" : "No valid pass found")
    };

    setRecentScans([scanResult, ...recentScans]);
    setManualScanForm({ vehiclePlate: "", gateId: "", notes: "" });
    
    if (hasValidPass) {
      toast.success(`‚úÖ Access granted for ${scanResult.vehiclePlate} at ${selectedGate.name}`);
    } else {
      toast.error(`‚ùå Access denied for ${scanResult.vehiclePlate} - No valid pass`);
    }
  };

  const simulateQrScan = () => {
    // Simulate scanning a QR code
    const mockQrCodes = ["PARK-MH12AB1234", "PARK-MH14CD5678", "PARK-MH16EF9012", "INVALID-CODE"];
    const randomQr = mockQrCodes[Math.floor(Math.random() * mockQrCodes.length)];
    setQrScanForm({...qrScanForm, qrCode: randomQr});
    toast.info("QR Code scanned!");
  };

  const getResultColor = (result: string) => {
    return result === "allow" ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800";
  };

  const getModeIcon = (mode: string) => {
    return mode === "qr" ? "üì±" : "‚å®Ô∏è";
  };

  const getDirectionIcon = (direction: string) => {
    return direction === "entry" ? "üöó‚Üí" : "üöô‚Üê";
  };

  const formatTimeAgo = (timestamp: string) => {
    const now = new Date();
    const scanTime = new Date(timestamp);
    const diffMs = now.getTime() - scanTime.getTime();
    const diffMins = Math.floor(diffMs / (1000 * 60));
    
    if (diffMins < 1) return "Just now";
    if (diffMins < 60) return `${diffMins}m ago`;
    const diffHours = Math.floor(diffMins / 60);
    if (diffHours < 24) return `${diffHours}h ago`;
    return scanTime.toLocaleDateString();
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      {/* Header */}
      <header className="bg-white dark:bg-gray-800 shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-2">
                <div className="w-8 h-8 bg-blue-600 rounded-lg flex items-center justify-center">
                  <span className="text-white font-bold text-sm">P</span>
                </div>
                <h1 className="text-xl font-semibold text-gray-900 dark:text-white">
                  ParkNest Pro
                </h1>
              </div>
              <Badge variant="secondary" className="ml-4">
                Scan Operations
              </Badge>
            </div>
            <div className="flex items-center space-x-4">
              <div className="text-sm text-gray-500 dark:text-gray-400">
                {currentTime.toLocaleString('en-IN', {
                  weekday: 'short',
                  year: 'numeric',
                  month: 'short',
                  day: 'numeric',
                  hour: '2-digit',
                  minute: '2-digit',
                  second: '2-digit'
                })}
              </div>
              <Button variant="outline" size="sm" onClick={() => window.history.back()}>
                ‚Üê Back to Dashboard
              </Button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
            Scan Operations
          </h2>
          <p className="text-gray-600 dark:text-gray-400">
            Process vehicle entry and exit through QR code scanning or manual entry.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Scanning Interface */}
          <div className="lg:col-span-2 space-y-6">
            <Tabs defaultValue="qr" className="space-y-6">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="qr">QR Code Scan</TabsTrigger>
                <TabsTrigger value="manual">Manual Entry</TabsTrigger>
              </TabsList>

              <TabsContent value="qr" className="space-y-6">
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center space-x-2">
                      <span>üì±</span>
                      <span>QR Code Scanner</span>
                    </CardTitle>
                    <CardDescription>Scan vehicle pass QR codes for quick entry/exit</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div>
                      <Label htmlFor="qrCode">QR Code Data</Label>
                      <div className="flex space-x-2">
                        <Input
                          id="qrCode"
                          placeholder="Scan QR code or enter manually..."
                          value={qrScanForm.qrCode}
                          onChange={(e) => setQrScanForm({...qrScanForm, qrCode: e.target.value})}
                        />
                        <Button variant="outline" onClick={simulateQrScan}>
                          üì∑ Simulate Scan
                        </Button>
                      </div>
                    </div>

                    <div>
                      <Label htmlFor="qrGate">Select Gate</Label>
                      <Select value={qrScanForm.gateId} onValueChange={(value) => setQrScanForm({...qrScanForm, gateId: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Choose gate" />
                        </SelectTrigger>
                        <SelectContent>
                          {mockGates.filter(gate => gate.active).map((gate) => (
                            <SelectItem key={gate.id} value={gate.id}>
                              {getDirectionIcon(gate.direction)} {gate.name}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>

                    <div>
                      <Label htmlFor="qrNotes">Notes (Optional)</Label>
                      <Textarea
                        id="qrNotes"
                        placeholder="Add any additional notes..."
                        value={qrScanForm.notes}
                        onChange={(e) => setQrScanForm({...qrScanForm, notes: e.target.value})}
                        rows={2}
                      />
                    </div>

                    <Button onClick={handleQrScan} className="w-full" size="lg">
                      Process QR Scan
                    </Button>
                  </CardContent>
                </Card>
              </TabsContent>

              <TabsContent value="manual" className="space-y-6">
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center space-x-2">
                      <span>‚å®Ô∏è</span>
                      <span>Manual Entry</span>
                    </CardTitle>
                    <CardDescription>Manually enter vehicle details for entry/exit</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div>
                      <Label htmlFor="vehiclePlate">Vehicle Plate Number</Label>
                      <Input
                        id="vehiclePlate"
                        placeholder="e.g., MH12AB1234"
                        value={manualScanForm.vehiclePlate}
                        onChange={(e) => setManualScanForm({...manualScanForm, vehiclePlate: e.target.value})}
                      />
                    </div>

                    <div>
                      <Label htmlFor="manualGate">Select Gate</Label>
                      <Select value={manualScanForm.gateId} onValueChange={(value) => setManualScanForm({...manualScanForm, gateId: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Choose gate" />
                        </SelectTrigger>
                        <SelectContent>
                          {mockGates.filter(gate => gate.active).map((gate) => (
                            <SelectItem key={gate.id} value={gate.id}>
                              {getDirectionIcon(gate.direction)} {gate.name}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>

                    <div>
                      <Label htmlFor="manualNotes">Notes (Optional)</Label>
                      <Textarea
                        id="manualNotes"
                        placeholder="Add any additional notes..."
                        value={manualScanForm.notes}
                        onChange={(e) => setManualScanForm({...manualScanForm, notes: e.target.value})}
                        rows={2}
                      />
                    </div>

                    <Button onClick={handleManualScan} className="w-full" size="lg">
                      Process Manual Entry
                    </Button>
                  </CardContent>
                </Card>
              </TabsContent>
            </Tabs>
          </div>

          {/* Gate Status */}
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Gate Status</CardTitle>
                <CardDescription>Current status of all gates</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {mockGates.map((gate) => (
                    <div key={gate.id} className="flex items-center justify-between p-3 border rounded-lg">
                      <div>
                        <div className="font-medium text-sm">{gate.name}</div>
                        <div className="text-xs text-gray-500 flex items-center space-x-1">
                          <span>{getDirectionIcon(gate.direction)}</span>
                          <span>{gate.direction}</span>
                        </div>
                      </div>
                      <Badge className={gate.active ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"}>
                        {gate.active ? "Active" : "Inactive"}
                      </Badge>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Quick Stats</CardTitle>
                <CardDescription>Today's scanning activity</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Total Scans</span>
                    <span className="font-semibold">{recentScans.length}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Successful</span>
                    <span className="font-semibold text-green-600">
                      {recentScans.filter(scan => scan.result === "allow").length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Denied</span>
                    <span className="font-semibold text-red-600">
                      {recentScans.filter(scan => scan.result === "deny").length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">QR Scans</span>
                    <span className="font-semibold">
                      {recentScans.filter(scan => scan.mode === "qr").length}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Manual Entry</span>
                    <span className="font-semibold">
                      {recentScans.filter(scan => scan.mode === "manual").length}
                    </span>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Recent Activity */}
        <Card className="mt-8">
          <CardHeader>
            <CardTitle>Recent Scan Activity</CardTitle>
            <CardDescription>Latest entry and exit scans</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Time</TableHead>
                    <TableHead>Vehicle</TableHead>
                    <TableHead>Gate</TableHead>
                    <TableHead>Type</TableHead>
                    <TableHead>Mode</TableHead>
                    <TableHead>Result</TableHead>
                    <TableHead>Operator</TableHead>
                    <TableHead>Notes</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {recentScans.slice(0, 10).map((scan) => (
                    <TableRow key={scan.id}>
                      <TableCell className="text-sm">
                        {formatTimeAgo(scan.timestamp)}
                      </TableCell>
                      <TableCell className="font-medium">{scan.vehiclePlate}</TableCell>
                      <TableCell className="text-sm">{scan.gateName}</TableCell>
                      <TableCell>
                        <div className="flex items-center space-x-1">
                          <span>{getDirectionIcon(scan.direction)}</span>
                          <span className="text-sm">{scan.direction}</span>
                        </div>
                      </TableCell>
                      <TableCell>
                        <div className="flex items-center space-x-1">
                          <span>{getModeIcon(scan.mode)}</span>
                          <span className="text-sm">{scan.mode}</span>
                        </div>
                      </TableCell>
                      <TableCell>
                        <Badge className={getResultColor(scan.result)}>
                          {scan.result}
                        </Badge>
                      </TableCell>
                      <TableCell className="text-sm">{scan.operatorName}</TableCell>
                      <TableCell className="text-sm text-gray-500">
                        {scan.notes || "-"}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </CardContent>
        </Card>
      </main>
    </div>
  );
}
